import React from 'react'; 
import "./App.css";
import * as THREE from "three"; 
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";

// https://szczecinski.eu/docs/react/komponenty/lifecycle
export default class App extends React.Component {
  constructor(props) {
    // wywołany konstruktor jak pierwszy raz komponent
    // dodany do JSX, otrzymuje przekazane dane w postaci 
    // propsów, które przekazuje poprzez super() dalej
    // Tutaj ustawia się początkowy stan aplikacji
    // inicjuje zmienne klasy
    // NIE wywołuje się tu funkcji asynchronicznych
      super(props);
      // referencja która pozwoli nam dodac canvas do div
      this.mountRef = React.createRef();  

      this.lastTime = 0;  
  }

  // ******************* COMPONENT LIFECYCLE ******************* //
  // komponent jest już renderowany w DOM
  componentDidMount() {
    // wywołana raz w cyklu zycia zaraz po pierwszym wywołaniu render
    // Idealna do operacji asynchronicznych!!
    // nie aktualizujemy tutaj stanu komponentu
    this.componentVisible = true;

      // pobieramy referencję do div
      this.mount = this.mountRef.current; 

      this.scene = new THREE.Scene();
      this.camera = new THREE.PerspectiveCamera( 
        75, window.innerWidth / window.innerHeight, 
        0.1, 1000 );
      
      this.camera.position.set(5, 7, 7);
      this.camera.lookAt(0,0,0);
      this.scene.add(this.camera);

      const cubeTextures = new THREE.CubeTextureLoader()
      .setPath("/assets/textures/cube1/")
      .load( [
          "px.jpg",
          "nx.jpg",
          "py.jpg",
          "ny.jpg",
          "pz.jpg",
          "nz.jpg"
      ]);

      this.scene.background = cubeTextures;

      this.renderer = new THREE.WebGLRenderer({  
          antialias: false,
      });

      this.renderer.setSize( window.innerWidth, window.innerHeight );
      // zwykle 1 dla PC ale na mobile z RETINA może byc wiecej wiec aby
      // nie mieli ząbków na obiektach będzie renderować tyle ile maja pikseli
      this.renderer.setPixelRatio( window.devicePixelRatio ); // aby nie malować zbyt dużo piseli na retina
      this.mount.appendChild(this.renderer.domElement);

      const orbitControls = new OrbitControls(
         this.camera, this.renderer.domElement );

      const gridXZ = new THREE.GridHelper(10,10);
      this.scene.add(gridXZ);

      //this.scene.add( geometryHelper.createAxesHelper(5) );
 

      
      const geometry = new THREE.BoxGeometry();
      const material = new THREE.MeshBasicMaterial( {
          wireframe: true, 
          color: 0x00ff00 
        } );
      this.cube = new THREE.Mesh( geometry, material );
      this.scene.add( this.cube );  
      

      const axesHelper = new THREE.AxesHelper(5);
      this.scene.add(axesHelper) 
        
      // Init any event listeners
      window.addEventListener('mousemove', this.mouseMove);
      window.addEventListener('resize', this.handleResize);
 
      this.renderScene();
  }


  componentDidUpdate(prevProps, prevState) {  
    // tutaj komponent otrzymuej aktualizacje
    // Tutaj wywołać akcje synchroniczne
    // reagowanie na zmiany w DOM
    // dodanie timerow, nsluchiwanie zdarzeń
    // NIE aktualizujemy tutaj stanu komponentów
      // Pass updated props to 
      //const newValue = this.props.whateverProperty; 
  }

  componentWillUnmount() {
    // komponent ma byc usunięty i tutaj mamy okazje aby posprzątać
    // tutak kasujemy timery, sprżątamy
    // 
    this.componentVisible = false;
      this.mount.removeChild(this.renderer.domElement);
      // Remove any event listeners
      window.removeEventListener('mousemove', this.mouseMove);
      window.removeEventListener('resize', this.handleResize);
  }

  // ******************* nasłuchiwanie zdarzeń ******************* //
  mouseMove = (event) => {
      // console.log(event.clientX, event.clientY);
  }

  handleResize = () => {
    console.log("resize");
    this.camera.aspect = window.innerWidth / window.innerHeight;
    this.camera.updateProjectionMatrix();
    this.renderer.setSize(window.innerWidth, window.innerHeight);
  }; 

  renderScene = (time) => { 
    if (!time) time = 0;
    time *= 0.001; // na sekundy 

    const deltaTime = time - this.lastTime;
    this.lastTime = time;

    if (this.componentVisible) {
      this.cube.rotation.y += 0.01;

      this.renderer.render(this.scene, this.camera);
      requestAnimationFrame(this.renderScene);
    }

    
  }

  render() {
      return (
          <div className="canvasContainer" ref={this.mountRef}> 
             
          </div>
      );
  } 
}
 
